@import url(https://fonts.googleapis.com/css2?family=Gaegu:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Gaegu:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Gaegu:wght@700&display=swap);
*,
::before,
::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: Arial, Helvetica, sans-serif;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
  margin-top: 20px;
  -webkit-animation: App-logo-spin infinite 5s linear;
          animation: App-logo-spin infinite 5s linear;
}

.background {
  background: url("https://i.imgur.com/gb0Mznv.png") no-repeat center center
    fixed;
  background-size: cover;
  height: 100%;
  width: 100%;
}

.slogan {
  text-align: center;
  align-items: center;
  align-content: center;
  font-style: italic;
  font-family: Gaegu;
  margin: auto;
  color: white;
  text-shadow: 3px 3px black;
}

.bottom {
  margin: auto;
  display: flex;
  width: 80%;
  align-items: center;
  background-color: blue;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotateY(45deg);
            transform: rotateY(45deg);
  }
  to {
    -webkit-transform: rotateY(225deg);
            transform: rotateY(225deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotateY(45deg);
            transform: rotateY(45deg);
  }
  to {
    -webkit-transform: rotateY(225deg);
            transform: rotateY(225deg);
  }
}

.square {
  display: flex;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin-left: 5%;
  margin-right: 5%;
  align-items: center;
  background-color: #f5f3f3;
  border-radius: 5px;
  border-style: solid;
  border-width: 5px;
}
.textMain {
  margin: auto;
  text-align: center;
  font-family: Gaegu;
  padding: 20px;
}

nav {
  position: fixed;
  top: 0;
  width: 100%;
  color: black;
  background-color: #f5f3f3;
}

.myNav {
  padding: 0px;
  background-color: #f5f3f3;
}
.empty {
  color: #696969b2;
}

.empty:hover {
  color: #696969b2;
}

.buyButton {
  background-color: #216e30;
  border-color: #216e30;
  font-size: 25px;
  color: white;
  padding-left: 5px;
  padding-right: 5px;
}

.buyButton:hover {
  background-color: #1dde43;
  border-color: #1dde43;
}

h1 {
  font-family: Gaegu;
  color: #a702fe;
  font-size: 25px;
}

h1:hover {
  color: #ff00ff;
}

a {
  font-family: Gaegu;
  font-size: 25px;
  color: #f5f3f3;
}

@media screen and (max-width: 500px) {
  .list {
    flex-direction: column;
    height: auto;
  }
  .items:nth-child(1) {
    border-top: 1px solid rgba(255, 255, 255, 0.555);
    margin-top: 50px;
  }
  .items {
    width: 100%;
    border-top: 1px solid rgba(255, 255, 255, 0.555);
    text-align: center;
    margin-right: 0px;
    padding: 20px 0;
  }
  .btn {
    display: block;
  }
}

.bulletTitle {
  text-align: center;
  background-color: #f5f3f3;
  color: #ff00ff;
  font-weight: bolder;
}

.squareBullet {
  margin-left: 5%;
  margin-right: 5%;
  display: flex;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  background-color: #f5f3f3;
  border-radius: 5px;
  border-style: solid;
  border-width: 5px;
}

.textBullet {
  text-align: left;
  font-family: Gaegu;
}

.bulletItem {
  background-color: #f5f3f3;
}

@media screen and (min-width: 601px) {
  .textBullet {
    font-size: 30px;
  }
  .bulletTitle {
    font-size: 50px;
  }

  .textMain {
    font-size: 30px;
  }

  .slogan {
    font-size: 50px;
  }
}

/* If the screen size is 600px wide or less, set the font-size of <div> to 30px */
@media screen and (max-width: 600px) {
  .textBullet {
    font-size: 10px;
  }
  .bulletTitle {
    font-size: 20px;
  }

  .textMain {
    font-size: 10px;
  }

  .slogan {
    font-size: 20px;
  }
}

